import React, { useState } from 'react';
import { styled } from '@mui/system';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

const StyleBtn = styled(ToggleButton)(({ theme }) => ({
  minWidth: '115px',
  height: '40px',
  borderRadius: '5px',
  borderColor: theme.button.common.border.main,
  fontSize: '16px',
  transition: '.3s all ease',
  "&.Mui-selected, &.Mui-selected:hover": {
    background: '#555555',
    color: '#ffffff',
  },
}));


const CareerBtn = ({ alignment, setAlignment, ...props}) =>{

  const handleChange = (event, newAlignment) => {
    if (newAlignment !== null) {
      setAlignment(newAlignment);
    }
  };

  return (
    <>
      <ToggleButtonGroup
        value={alignment}
        exclusive
        onChange={handleChange}
        {...props}
      >
        <StyleBtn value={false}>없음</StyleBtn>
        <StyleBtn value={true}>있음</StyleBtn>
      </ToggleButtonGroup>
    </>
  );
}

export default CareerBtn;