import { MenuItem, Select } from '@mui/material';
import React from 'react';
import { useState } from 'react';
import DatepickerSM from '../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoLabelMD from '../../../../components/UI/Atoms/Info/InfoLabelMD';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { useInputs } from '../../../../hooks/input/useInputs';
import RegistrationAccordion from '../RegistrationAccordion';
import { useToggle } from '../../../../hooks/basic/useToggle';
import CareerBtn from '../../../../components/UI/Atoms/ButtonGroup/CareerBtn';

const selectItemStyle = {
  width: '354px',
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
};

const DriverLicense = () => {
  const [ alignment, setAlignment ] = useState(false)
  const [ expanded, Control ] = useToggle()
  const licenseType = ['1종 대형', '1종 보통', '2종 보통'];
  const [inputData, setInputData, handleChange] = useInputs('');

  // useDebug(inputData)

  return (
    <RegistrationAccordion title='운전 면허' expanded={expanded} onChange={Control}>
      <div className="mb-[30px]">
        <CareerBtn alignment={alignment} setAlignment={setAlignment}/>
      </div>
      { alignment && 
      <div>
        <div className="flex items-center mb-[10px]">
          <InfoLabelMD className="text-[16px] flex-none w-[111.5px]">운전면허</InfoLabelMD>
          <Select
            name='driver_license'
            placeholder="종류"
            value={inputData.driver_license || ''}
            onChange={handleChange}
            className="w-[354px] h-[40px] text-[15px]"
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
          >
            {React.Children.toArray(
              licenseType.map((level) => (
                <MenuItem sx={selectItemStyle} key={level} value={level}>
                  {level}
                </MenuItem>
              )),
            )}
          </Select>
          <input id='driver_license' value={inputData.driver_license || ''} hidden readOnly/>
        </div>

        <div className="flex items-center mb-[10px]">
          <InfoLabelMD className="text-[16px] w-[111.5px] flex-none">취득일</InfoLabelMD>
          <DatepickerSM
            name='driver_license_obtaining_date'
            length={125}
            value={inputData.driver_license_obtaining_date || ''}
            onChange={handleChange}
          ></DatepickerSM>
          <input id='driver_license_obtaining_date' value={inputData.driver_license_obtaining_date || ''} hidden readOnly/>
        </div>

      </div>}
    </RegistrationAccordion>
  );
};

export default DriverLicense;
