import { MenuItem, Select, Typography } from '@mui/material';
import React, { useMemo, useState } from 'react';
import InfoEditInput from '../../../../components/UI/Atoms/Input/InfoEditInput';
import { useInput } from '../../../../hooks/input/useInput';
import RegistrationAccordion from '../RegistrationAccordion';
import { useToggle } from '../../../../hooks/basic/useToggle';

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
};

const Certificate = ({ collapse }) => {

  const danList = useMemo(() => [
    { id:1, title:'1단' },
    { id:2, title:'2단' },
    { id:3, title:'3단' },
    { id:4, title:'4단' },
    { id:5, title:'5단' },
    { id:6, title:'6단' },
    { id:7, title:'7단' },
    { id:8, title:'8단' },
    { id:9, title:'9단' },
  ])

  const geubList = useMemo(() => [
    { id:1, title:'1급'},
    { id:2, title:'2급'},
    { id:3, title:'3급'},
  ])

  // handler
  const [expanded, Control] = useToggle(collapse ? false : true);

  return (
    <RegistrationAccordion title="단증 & 사범 자격증" expanded={expanded} onChange={Control}>
      <div className="mb-[20px]">
        <div className="mb-[10px]">
          <Typography className="text-[#00196A]">태권도 보유 단증</Typography>
        </div>
        <div className="flex gap-[5px]">
          <Select
            className="w-[20%] h-[40px] text-[15px]"
            placeholder="단"
            id="level"
            name="level"
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
            defaultValue=""
          >
            { React.Children.toArray(
              danList.map(item =>
                <MenuItem sx={selectItemStyle} value={item.id}>
                  {item.title}
                </MenuItem>
              )
            )}
          </Select>
          <InfoEditInput
            className="w-[80%]"
            placeholder={'단증 번호 입력'}
            sx={{ '& .MuiInputBase-input': { height: '30px', width: '214px', fontSize: '15px' } }}
            id="tkd_level_number"
            name="tkd_level_number"
            type="number"
          />
        </div>
      </div>
      <div>
        <div className="mb-[10px]">
          <Typography className="text-[#00196A]">사범 자격증</Typography>
        </div>
        <div className="flex gap-[5px]">
          <Select
            className="w-[20%] h-[40px] text-[15px]"
            placeholder="급"
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
            id="instructor_license_grade"
            name="instructor_license_grade"
            defaultValue=""
          >
            { React.Children.toArray(
              geubList.map(item =>
                <MenuItem sx={selectItemStyle} value={item.id}>
                  {item.title}
                </MenuItem>
              )
            )}
          </Select>
          <InfoEditInput
            className="w-[80%]"
            placeholder={'자격증 번호 입력'}
            id="instructor_license_number"
            name="instructor_license_number"
            sx={{ '& .MuiInputBase-input': { height: '30px', width: '214px', fontSize: '15px' } }}
          />
        </div>
      </div>
    </RegistrationAccordion>
  );
};
export default Certificate;
