import React from 'react'
import styled from 'styled-components'

const SpanStyle = styled.span`
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 7px;
  right: 7px;
  background-image: url('https://tkdw-img.s3.ap-northeast-2.amazonaws.com/profileUploadBtn.svg');
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
`

const ProfileEditUploadBtn = ({...props}) => {
  return (
    <SpanStyle {...props} />
  )
}

export default ProfileEditUploadBtn