// import { CheckBox } from '@mui/icons-material';
import Checkbox from '@mui/material/Checkbox';
import { FormControlLabel, FormGroup, MenuItem, Select } from '@mui/material';
import { borderColor, borderRadius } from '@mui/system';
import React, { useState } from 'react';
import CreateBtnMD from '../../../../components/UI/Atoms/Button/CreateBtnMD';
import DatepickerSM from '../../../../components/UI/Atoms/Datepicker/DatepickerSM';
import InfoLabelMD from '../../../../components/UI/Atoms/Info/InfoLabelMD';
import InfoEditInput from '../../../../components/UI/Atoms/Input/InfoEditInput';
import { useInputs } from '../../../../hooks/input/useInputs';
import { useDebug } from '../../../../hooks/basic/useDebug';
import RegistrationAccordion from '../RegistrationAccordion';
import { useToggle } from '../../../../hooks/basic/useToggle';

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
};

const UnivInfo = () => {
  const gradeList = ['4.0', '4.3', '4.5', '5.0'];
  const [ expanded, onChange ] = useToggle()

  const [addMajor, setAddMajor] = useState(false);

  const [inputData, setInputData, handleChange] = useInputs('');

  // handler

  const handleAddMajor = () => {
    setAddMajor(true);
  };

  const handleDeleteMajor = () => {
    setAddMajor(false);
  };

  const handleCheck = (e) => {
    let { name } = e.target;
    if(e.target.checked == true){
      setInputData({...inputData, [name]: 'true'})
    }
    if(e.target.checked == false){
      setInputData({...inputData, [name]: 'false'})
    }
  }

  // useDebug(inputData);

  return (
    <RegistrationAccordion title='최종 학력' expanded={expanded} onChange={onChange}>

      <div className="flex flex-col gap-[10px]">

        {/* <div className="flex items-center justify-between">
          <InfoLabelMD className="text-[16px] flex-none">대학</InfoLabelMD>
          <Select
            name="univ"
            placeholder="학교구분"
            value={inputData.univ || ''}
            onChange={handleChange}
            className="w-[354px] h-[40px] text-[15px]"
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
          >
            <MenuItem sx={selectItemStyle} value={'대학(4년)'}>
              대학(4년)
            </MenuItem>
            <MenuItem sx={selectItemStyle} value={'대학(2년)'}>
              대학(2년)
            </MenuItem>
          </Select>
        </div> */}

        <div className="flex items-center justify-between">
          <InfoLabelMD className="text-[16px] flex-none">학교명</InfoLabelMD>
          <InfoEditInput
            id='school_name'
            name="school_name"
            placeholder={'학교명'}
            value={inputData.school_name || ''}
            onChange={handleChange}
            sx={{ '& .MuiInputBase-input': { height: '30px', width: '334px', fontSize: '15px' } }}
          ></InfoEditInput>
        </div>

        <div className="flex items-center">
          <InfoLabelMD className="text-[16px] w-[111.5px] flex-none" >재학 기간</InfoLabelMD>
          <div className="flex items-center gap-[10px]">
            <DatepickerSM
              id="admission_date"
              name="admission_date"
              length={125}
              // placeholder={'입학년월'}
              value={inputData.admission_date || ''}
              onChange={handleChange}
            ></DatepickerSM>
            <input id="admission_date" value={inputData.admission_date || ''} readOnly hidden />
            ~
            <DatepickerSM
              id="graduated_date"
              name="graduated_date"
              length={125}
              // placeholder={'졸업년월'}
              value={inputData.graduated_date || ''}
              onChange={handleChange}
            ></DatepickerSM>
            <input id="graduated_date" value={inputData.graduated_date || ''} readOnly hidden />
            {/* <FormGroup>
              <FormControlLabel
                name='편입여부'
                value='편입'
                sx={{ '& .MuiFormControlLabel-label': { fontSize: '15px' } }}
                className="mr-0"
                control={<Checkbox />}
                label="편입"
                onChange={handleCheck}
              />
            </FormGroup> */}
          </div>
        </div>

        {/* <div className="flex items-center justify-between">
          <InfoLabelMD className="text-[16px] flex-none">졸업여부</InfoLabelMD>
          <Select
            name="graduated"
            placeholder="졸업여부"
            value={inputData.graduated || ''}
            onChange={handleChange}
            className="w-[354px] h-[40px] text-[15px]"
            sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
          >
            <MenuItem sx={selectItemStyle} value={'예정'}>
              예정
            </MenuItem>
            <MenuItem sx={selectItemStyle} value={'졸업'}>
              졸업
            </MenuItem>
          </Select>
        </div> */}

        <div className="flex items-center justify-between">
          <InfoLabelMD className="text-[16px] flex-none">전공</InfoLabelMD>
          <div className="flex justify-between gap-[5px]">
            <InfoEditInput
              id="major"
              name="major"
              placeholder={'전공학과 입력'}
              value={inputData.major || ''}
              onChange={handleChange}
              sx={{ '& .MuiInputBase-input': { height: '30px', width: '334px', fontSize: '15px' } }}
            ></InfoEditInput>
            {/* <CreateBtnMD className="w-[115px] text-[14px]" onClick={handleAddMajor}>
              + 전공 추가
            </CreateBtnMD> */}
          </div>
        </div>

        {/* {addMajor && (
          <div className="flex items-center justify-between">
            <InfoLabelMD className="text-[16px] flex-none">전공</InfoLabelMD>
            <div className="flex justify-between gap-[5px]">
              <InfoEditInput
                name="major2"
                placeholder={'전공학과 입력'}
                value={inputData.major2 || ''}
                onChange={handleChange}
                sx={{
                  '& .MuiInputBase-input': { height: '30px', width: '214px', fontSize: '15px' },
                }}
              ></InfoEditInput>
              <CreateBtnMD className="w-[115px] text-[14px]" onClick={handleDeleteMajor}>
                삭제
              </CreateBtnMD>
            </div>
          </div>
        )} */}

        <div className="flex items-center justify-between">
          <InfoLabelMD className="text-[16px] flex-none">학점</InfoLabelMD>
          <div className="w-[354px] flex gap-[5px]">
            <InfoEditInput
              id="grades"
              name="grades"
              type="number"
              placeholder={'학점 입력'}
              value={inputData.grades || ''}
              onChange={handleChange}
              className="w-[50%]"
              sx={{ '& .MuiInputBase-input': { height: '30px', width: '214px', fontSize: '15px' } }}
            ></InfoEditInput>
            <Select
              id="total_grades"
              name="total_grades"
              placeholder="총점"
              value={inputData.total_grades || '4.5'}
              onChange={handleChange}
              style={{ borderRadius: '5px', borderColor: '#dddddd', fontSize: '15px' }}
              className="w-[50%] h-[40px] text-[15px]"
              sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
            >
              {React.Children.toArray(
                gradeList.map((value, idx) => (
                  <MenuItem key={idx} sx={selectItemStyle} value={value}>
                    {value}
                  </MenuItem>
                )),
              )}
            </Select>
            
          </div>
        </div>
        
      </div>
    </RegistrationAccordion>
  );
};

export default UnivInfo;
