import React, { useState } from 'react';
import { Select, MenuItem } from '@mui/material';
import CreateBtnMD from '../../../../components/UI/Atoms/Button/CreateBtnMD';
import BlackBtnMD from '../../../../components/UI/Atoms/Button/BlackBtnMD';
import InfoLabelMD from '../../../../components/UI/Atoms/Info/InfoLabelMD';
import InfoEditInput from '../../../../components/UI/Atoms/Input/InfoEditInput';
import { useDebug } from '../../../../hooks/basic/useDebug';
import { useToggle } from '../../../../hooks/basic/useToggle';
import RegistrationAccordion from '../RegistrationAccordion';
import BlackBtnSM from '../../../../components/UI/Atoms/Button/BlackBtnSM';

const selectItemStyle = {
  height: '30px',
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  '&:hover': {
    fontWeight: 'bold',
    backgroundColor: '#f3f3f3',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
  '&:focus': {
    fontWeight: 'bold',
    '&:before': {
      content: '""',
      backgroundImage: 'url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/check_select.png)',
      width: '15px',
      height: '15px',
      position: 'absolute',
      right: '10px',
    },
  },
};

const ExtraCertification = () => {
  const levelList = ['없음','1급','2급','3급'];
  const [ expanded, onChange ] = useToggle()

  const form = { certification_name: '', level: '', certification_number: '' };
  const [list, setList] = useState([form]);

  const handleAdd = (e) => {
    setList([...list, form]);
  };

  const handleDelete = (e) => {
    let { id } = e.currentTarget;
    let temp = list.filter((v, idx) => {
      return id != idx;
    });
    setList(temp);
  };

  const handleChange = (e) => {
    let { id, name, value } = e.currentTarget;
    let temp = [...list];
    temp[id] = { ...temp[id], [name]: value };
    setList(temp);
  };

  const handleSelect = (e, index) => {
    let {name, value} = e.target;
    let temp = [...list];
    temp[index] = {...temp[index], [name]:value};
    setList(temp);
  };

  // useDebug(list);


  return (
    <RegistrationAccordion title='기타 자격증' expanded={expanded} onChange={onChange}>

      {React.Children.toArray(
        list.map((item, index) => (
          <div>
            {index !== 0 && (
              <>
                <div className="w-full h-[1px] bg-[#aaaaaa] mt-[10px] mb-[20px]"></div>
                <div className="text-right mb-[10px]">
                  <BlackBtnSM id={`${index}`} onClick={handleDelete}>
                    삭제
                  </BlackBtnSM>
                </div>
              </>
            )}

            <div className="flex items-center justify-between mb-[10px]">
              <InfoLabelMD className="text-[16px] flex-none">자격증명</InfoLabelMD>
              <InfoEditInput
                id={`${index}`}
                name="license_name"
                placeholder="자격증명 입력"
                value={item.license_name || ''}
                onChange={handleChange}
                sx={{
                  '& .MuiInputBase-input': { height: '30px', width: '334px', fontSize: '15px' },
                }}
              ></InfoEditInput>
            </div>

            <div className="flex items-center mb-[10px]">
              <InfoLabelMD className="text-[16px] flex-none w-[111.5px]">급</InfoLabelMD>
              <Select
                // id={`${index}`}
                name="license_grade"
                placeholder="급"
                value={item.license_grade || ''}
                onChange={(e)=>handleSelect(e, index)}
                className="w-[109px] h-[40px] text-[15px]"
                sx={{ '& .MuiOutlinedInput-input': { paddingLeft: '10px' } }}
              >
                {React.Children.toArray(
                  levelList.map((level) => (
                    <MenuItem sx={selectItemStyle} key={level} id={level} value={level}>
                      {level}
                    </MenuItem>
                  )),
                )}
              </Select>
            </div>

            <div className="flex items-center justify-between mb-[20px]">
              <InfoLabelMD className="text-[16px] flex-none">일련번호</InfoLabelMD>
              <InfoEditInput
                id={`${index}`}
                name="license_number"
                placeholder={'일련번호 입력'}
                value={item.license_number || ''}
                onChange={handleChange}
                sx={{
                  '& .MuiInputBase-input': { height: '30px', width: '334px', fontSize: '15px' },
                }}
              ></InfoEditInput>
            </div>
          </div>
        ))
      )}

      <div className="text-center mt-[30px]">
        <CreateBtnMD onClick={handleAdd} className="text-[14px]">
          + 자격증 추가
        </CreateBtnMD>
      </div>

    </RegistrationAccordion>
  );
};

export default ExtraCertification;

